import React, { useState } from 'react';
import axios from 'axios';
import { Link, useNavigate, Navigate, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { loginSuccess } from '../redux/authSlice';
import '../styles/Login.css';
import logo from '../assets/shuttleplay_main_logo.png';

function LoginPage() {
  const navigate = useNavigate();
  const location = useLocation();
  const from = location.state?.from?.pathname || '/main';
  const dispatch = useDispatch();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const { isAuthenticated } = useSelector(state => state.auth);

  if (isAuthenticated) {
    return <Navigate to={from} replace />;
  }

  const handleLogin = async (e) => {
    e.preventDefault();
    setErrorMessage('');

    try {
      const response = await axios.post('/login', {
        email,
        password
      }, {
        headers: { 'Content-Type': 'application/json' },
        withCredentials: true
      });

      //  
      dispatch(loginSuccess({ userId: response.data.userId }));
      localStorage.setItem('user', JSON.stringify({ userId: response.data.userId }));

      //   
      navigate(from, { replace: true });
    } catch (error) {
      setErrorMessage(error.response?.data?.error || ' .');
    }
  };

  return (
    <div className="login-page">
      <div className="login-wrapper">
        <button type="button" className="back-btn" onClick={() => navigate(-1)}>
          <i className="bi bi-arrow-left"></i>
        </button>

        <Link to="/main">
          <img src={logo} alt="" className="logo-img"/>
        </Link>

        <h2 className="subtitle"></h2>

        <form onSubmit={handleLogin} className="login-form">
          <label htmlFor="email">(email)</label>
          <input
            type="email"
            id="email"
            name="email"
            placeholder="()"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />

          <label htmlFor="password"></label>
          <input
            type="password"
            id="password"
            name="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />

          <div className="options">
            <label><input type="checkbox" name="remember" /></label>
            <a href="#!" className="find-link"></a>
          </div>

          {errorMessage && (
            <div className="login-form-error">{errorMessage}</div>
          )}

          <button type="submit" className="login-submit-btn"></button>
        </form>

        <div className="login-divider">
          <hr className="line" />
          <span className="or"></span>
          <hr className="line" />
        </div>

        <div className="social-buttons-grid">
          <button type="button" className="social-btn naver"> </button>
          <button type="button" className="social-btn kakao"> </button>
          <button type="button" className="social-btn google"> </button>
          <button type="button" className="social-btn apple"> </button>
        </div>

        <p className="register-bottom"> ?
          <Link to="/register" className="register-link"></Link>
        </p>
      </div>
    </div>
  );
}

export default LoginPage;
