import React, { useState } from 'react';
import { useNavigate, Link, Navigate } from 'react-router-dom';
import '../styles/Register.css';
import logo from '../assets/shuttleplay_main_logo.png';
import axios from 'axios';
import { useSelector } from 'react-redux';

function Register() {
  const navigate = useNavigate();
  const [step, setStep] = useState(1);
  const [error, setError] = useState('');
  const [form, setForm] = useState({
    name: '',
    nickname: '',
    gender: '',
    email: '',
    phone: '',
    password: '',
    rankStr: '',
    ageGroup: '',
    playStyle: '',
    gameType: '',
  });

  const { isAuthenticated } = useSelector(state => state.auth);

  if (isAuthenticated) {
    return <Navigate to="/main" replace/>;
  }

  const handleChange = e => {
    const { name, value } = e.target;
    setForm({ ...form, [name]: value });
  };

  const next = () => {
    const { name, email, phone, password, nickname } = form;
    const nameReg  = /^[-a-zA-Z]{2,}$/;
    const emailReg = /^[\w.+-]+@[a-z\d-]+(\.[a-z\d-]+)+$/i;
    const phoneReg = /^01[016789]\d{7,8}$/;
    const passReg  = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{8,20}$/;
    
    if (!name || !email || !phone || !password || !nickname) {
      setError('    .');
      return;
    }

    if (!nameReg.test(name)) {
      setError('  2  .');
      return;
    }

    if (!emailReg.test(email)) {
      setError('   .');
      return;
    }

    if (!phoneReg.test(phone)) {
      setError('  01012345678  .');
      return;
    }

    if (!passReg.test(password)) {
      setError(' 8-20,     .');
      return;
    }

    setError('');
    setStep(2);
  };

  const back = () => setStep(1);

  const handleSubmit = async e => {
    e.preventDefault();
    try {
      await axios.post('/api/users/register', form);
      navigate('/register/success');
    } catch (err) {
      setError(' .   .');
    }
  };

  return (
    <div className="register-page">
      <button type="button" className="Back-btn" onClick={() => navigate(-1)}>
        <i className="bi bi-arrow-left"></i>
      </button>

      <div className="register-wrapper">
        <Link to="/main">
          <img src={logo} alt="" className="logo-img"/>
        </Link>

        <h2 className="subtitle"></h2>

        <div className="progress-bar">
          <div className="progress" style={{ width: step === 1 ? '50%' : '100%' }}/>
        </div>

        {step === 1 && (
          <form className="register-form" onSubmit={e => e.preventDefault()}>
            <label htmlFor="name"></label>
            <input id="name" name="name" value={form.name} onChange={handleChange} placeholder="" required/>

            <label htmlFor="email"></label>
            <input id="email" name="email" type="email" value={form.email} onChange={handleChange} placeholder="abcd@abcd.com" required/>

            <label htmlFor="phone"></label>
            <input id="phone" name="phone" value={form.phone} onChange={handleChange} placeholder="01012345678" required/>

            <label htmlFor="password"></label>
            <input id="password" name="password" type="password" value={form.password} onChange={handleChange} placeholder="8-20, /  " required/>

            <label htmlFor="nickname"></label>
            <input id="nickname" name="nickname" value={form.nickname} onChange={handleChange} required/>

            {error && <p className="form-error">{error}</p>}

            <div className="btn-right">
              <button type="button" className="primary-btn" onClick={next}></button>
            </div>
          </form>
        )}

        {step === 2 && (
          <form className="register-form" onSubmit={handleSubmit}>
            <label htmlFor="gender"></label>
            <select id="gender" name="gender" value={form.gender} onChange={handleChange} required>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>

            <label htmlFor="ageGroup"></label>
            <select id="ageGroup" name="ageGroup" value={form.ageGroup} onChange={handleChange} required>
              <option value=""></option>
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="30">30</option>
              <option value="40">40</option>
              <option value="50">50</option>
              <option value="60">60</option>
              <option value="70">70</option>
            </select>

            <label htmlFor="rankStr"></label>
            <select id="rankStr" name="rankStr" value={form.rankStr} onChange={handleChange} required>
              <option value=""></option>
              <option value="E">E</option>
              <option value="D">D</option>
              <option value="C">C</option>
              <option value="B">B</option>
              <option value="A">A</option>
              <option value="S">S</option>
              <option value="SS">SS</option>
            </select>

            <label htmlFor="playStyle"> </label>
            <select id="playStyle" name="playStyle" value={form.playStyle} onChange={handleChange} required>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>

            <label htmlFor="gameType"> </label>
            <select id="gameType" name="gameType" value={form.gameType} onChange={handleChange} required>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>

            <div className="step-btn-group">
              <button type="button" className="secondary-btn" onClick={back}></button>
              <button type="submit" className="primary-btn"></button>
            </div>
          </form>
        )}

        <p className="login-bottom">  ?
          <Link to="/login" className="login-bottom-link"></Link>
        </p>
      </div>
    </div>
  );
}

export default Register;
