import React, { useState, useEffect, useCallback } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../styles/PreMatchingGameRoom.css';
import { MapPin, Clock, Users, X } from "lucide-react";
import axios from 'axios';
import { useParams, useNavigate } from 'react-router-dom';

const rankColor = {
  SS: "purple",
  S: "red",
  A: "orange",
  B: "yellow",
  C: "green",
  D: "blue",
  E: "gray"
};

const gameTypeLabel = { 
  Singles: "", 
  Doubles: "" 
};

//   
const Badge = ({ children, color = "gray" }) => (
  <span className={`pm-badge pm-badge-${color}`}>{children}</span>
);

//   
const Button = ({ children, className = "", ...props }) => (
  <button className={`pm-btn ${className}`} {...props}>{children}</button>
);

// /   (  )
const SelectTypeModal = ({ open, onClose, onSelect }) => {
  if (!open) return null;
  return (
    <div className="pm-modal-bg">
      <div className="pm-modal-content" style={{maxWidth:330}}>
        <div className="pm-modal-header">
          <b>  </b>
          <Button className="pm-modal-close" onClick={onClose}><X size={18} /></Button>
        </div>
        <div style={{ display: 'flex', flexDirection: 'column', gap: 18, marginTop: 26, marginBottom: 18 }}>
          <Button className="pm-modal-singles" onClick={() => { onSelect('Singles'); }}></Button>
          <Button className="pm-modal-doubles" onClick={() => { onSelect('Doubles'); }}></Button>
        </div>
      </div>
    </div>
  );
};

//    
const GameResultModal = ({ visible, onClose, room, onResultSaved, submitGameResult }) => {
  const [myScore, setMyScore] = useState('');
  const [opponentScore, setOpponentScore] = useState('');
  const [saving, setSaving] = useState(false);

  const myTeam = room?.players?.filter(p => p.team === "TEAM_A") || [];
  const opponentTeam = room?.players?.filter(p => p.team === "TEAM_B") || [];

  if (!visible || !room) return null;

  const userId = myTeam[0]?.id;
  const opponentId = opponentTeam[0]?.id;

  const handleSubmit = async () => {
    if (myScore === "" || opponentScore === "") {
      alert("   .");
      return;
    }
    if (!userId || !opponentId) {
      alert("    .");
      return;
    }
    setSaving(true);
    try {
      await submitGameResult({
        gameId: room.gameId,
        userId,
        opponentId,
        scoreTeamA: Number(myScore),
        scoreTeamB: Number(opponentScore)
      });
      if (onResultSaved) onResultSaved();
      onClose();
    } catch (e) {
      //  alert 
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="pm-modal-bg">
      <div className="pm-modal-content">
        <div className="pm-modal-header">
          <span role="img" aria-label="search" style={{fontSize:18}}></span>
          <b style={{marginLeft: 7}}>  </b>
          <Button className="pm-modal-close" onClick={onClose}><X size={18} /></Button>
        </div>
        <div className="pm-modal-detail">
          <div>{room.courtName} / {gameTypeLabel[room.gameType]}</div>
          <div>{room.createdAt.toLocaleDateString()} {room.createdAt.toLocaleTimeString()}</div>
        </div>
        <div className="pm-modal-teams">
          <div className="pm-modal-team">
            <h4>A </h4>
            {myTeam.map(user => (
              <div key={user.id} className="pm-modal-player-row">
                <span className="pm-avatar">{user.name.split(" ").map(n => n[0]).join("")}</span>
                <span>{user.name}</span>
                <Badge color={rankColor[user.rankLevel]}>
                  {user.rankLevel}
                </Badge>
              </div>
            ))}
            <div className="pm-modal-score-input">
              : <input
                type="number"
                value={myScore}
                min={0}
                onChange={e => setMyScore(e.target.value)}
                style={{width: 55, marginLeft: 4}}
                disabled={saving}
              />
            </div>
          </div>
          <div className="pm-modal-team">
            <h4>B </h4>
            {opponentTeam.map(user => (
              <div key={user.id} className="pm-modal-player-row">
                <span className="pm-avatar">{user.name.split(" ").map(n => n[0]).join("")}</span>
                <span>{user.name}</span>
                <Badge color={rankColor[user.rankLevel]}>
                  {user.rankLevel}
                </Badge>
              </div>
            ))}
            <div className="pm-modal-score-input">
              : <input
                type="number"
                value={opponentScore}
                min={0}
                onChange={e => setOpponentScore(e.target.value)}
                style={{width: 55, marginLeft: 4}}
                disabled={saving}
              />
            </div>
          </div>
        </div>
        <div className="pm-modal-footer">
          <Button
            className="pm-finish-btn"
            onClick={handleSubmit}
            disabled={saving}
          >{saving ? " ..." : " "}</Button>
          <Button
            className="pm-close-btn"
            onClick={onClose}
            style={{marginLeft:10, background:"#ececec", color:"#222"}}
            disabled={saving}
          ></Button>
        </div>
      </div>
    </div>
  );
};

//   
const TeamSettingModal = ({ visible, onClose, players, onSetTeams, gameId }) => {
  const [teamA, setTeamA] = useState([]);
  const [teamB, setTeamB] = useState([]);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    // :   A
    if (visible && players) {
      setTeamA(players.map(p => p.id));
      setTeamB([]);
    }
  }, [visible, players]);

  const handleTogglePlayer = (id) => {
    if (teamA.includes(id)) {
      setTeamA(teamA.filter(pid => pid !== id));
      setTeamB([...teamB, id]);
    } else {
      setTeamB(teamB.filter(pid => pid !== id));
      setTeamA([...teamA, id]);
    }
  };

  const handleAssignTeams = async () => {
    if (teamA.length === 0 || teamB.length === 0) {
      alert("A B   1   .");
      return;
    }
    setSaving(true);
    try {
      const teamAssignments = [
        ...teamA.map(userId => ({ userId: Number(userId), team: "TEAM_A" })),
        ...teamB.map(userId => ({ userId: Number(userId), team: "TEAM_B" })),
      ];
      await axios.patch(
        `/api/game/${gameId}/team`,
        teamAssignments,
        {
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert("  .");
      if (onSetTeams) onSetTeams(teamA, teamB);
      onClose();
    } catch (e) {
      if (e.response && e.response.data && typeof e.response.data === "string") {
        alert(e.response.data);
      } else {
        alert("    .");
      }
    } finally {
      setSaving(false);
    }
  };

  if (!visible) return null;

  return (
    <div className="pm-modal-bg">
      <div className="pm-modal-content">
        <div className="pm-modal-header">
          <b> </b>
          <Button className="pm-modal-close" onClick={onClose}><X size={18} /></Button>
        </div>
        <div className="pm-team-modal-row" style={{ margin: '24px 0' }}>
          <div className="pm-team-modal-col">
            <h4>A </h4>
            {players.filter(p => teamA.includes(p.id)).map(user => (
              <div key={user.id} className="pm-modal-player-row" style={{ cursor: 'pointer' }} onClick={() => handleTogglePlayer(user.id)}>
                <span className="pm-avatar">{user.name.split(" ").map(n => n[0]).join("")}</span>
                <span>{user.name}</span>
                <Badge color={rankColor[user.rankLevel]}>{user.rankLevel}</Badge>
              </div>
            ))}
          </div>
          <div className="pm-team-modal-col">
            <h4>B </h4>
            {players.filter(p => teamB.includes(p.id)).map(user => (
              <div key={user.id} className="pm-modal-player-row" style={{ cursor: 'pointer' }} onClick={() => handleTogglePlayer(user.id)}>
                <span className="pm-avatar">{user.name.split(" ").map(n => n[0]).join("")}</span>
                <span>{user.name}</span>
                <Badge color={rankColor[user.rankLevel]}>{user.rankLevel}</Badge>
              </div>
            ))}
          </div>
        </div>
        <div className="pm-modal-footer">
          <Button
            className="pm-finish-btn"
            onClick={handleAssignTeams}
            disabled={saving}
          >{saving ? " ..." : " "}</Button>
          <Button
            className="pm-close-btn"
            onClick={onClose}
            style={{ marginLeft: 10, background: "#ececec", color: "#222" }}
            disabled={saving}
          ></Button>
        </div>
      </div>
    </div>
  );
};

// 
export default function PreMatchingGameRoom() {
  const { roomId } = useParams();
  const navigate = useNavigate();
  const [currentTime, setCurrentTime] = useState(new Date());
  const [headerTitle, setHeaderTitle] = useState('');
  const [courtName, setCourtName]   = useState('');
  const [courtAddr, setCourtAddr]   = useState(''); 
  const [games, setGames] = useState([]);
  const [manualWaitlist, setManualWaitlist] = useState([]);
  const [autoWaitlist, setAutoWaitlist] = useState([]);
  const [selected, setSelected] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalRoom, setModalRoom] = useState(null);
  const [modalTypeOpen, setModalTypeOpen] = useState(false);
  const [teamModalOpen, setTeamModalOpen] = useState(false);
  const [teamModalRoomId, setTeamModalRoomId] = useState(null);
  const { userId: currentUserId } = JSON.parse(localStorage.getItem('user') || '{}');
  const [isAdmin, setIsAdmin] = useState(false);

  //     
  const fetchManualWaitlist = useCallback(async () => {
    try {
      const res = await axios.get(
        `/api/match/manual/queue-users?roomId=${roomId}`, 
        { 
          headers: { 'Content-Type': 'application/json' }, 
          withCredentials: true
        }
      );
      const queued = res.data?.queuedUsers || [];
      const formatted = queued.map(user => ({
        id: user.userId,
        name: user.nickname,
        rankLevel: user.rank,
        type: 'manual'
      }));
      setManualWaitlist(formatted);
    } catch (err) {
      console.error('    ', err);
    }
  }, [roomId]);

  //     
  const fetchAutoWaitlist = useCallback(async () => {
    try {
      const res = await axios.get(
        `/api/match/auto/queue-users?roomId=${roomId}`,
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true 
        }
      );
      const queued = res.data?.queuedUsers || [];
      const formatted = queued.map(user => ({
        id: user.userId,
        name: user.nickname,
        rankLevel: user.rank,
        gameType: Number(user.requiredMatchCount) === 2 ? 'Singles' : 'Doubles',
        type: 'auto'
      }));
      setAutoWaitlist(formatted);
    } catch (err) {
      console.error('    ', err);
    }
  }, [roomId]);

  //    
    const fetchGamelist = useCallback(async () => {
    try {
      const res = await axios.get(`/api/game-room/${roomId}/game-list`, {
        headers: { 'Content-Type': 'application/json' },
        withCredentials: true
      });

      const room = res.data.data;

      setHeaderTitle(room.title || '');
      setCourtName(room.locationName || '');
      setCourtAddr(room.locationAddress || '');
      setIsAdmin(Number(room.managerId) === Number(currentUserId));

      const parsedGames = (room.games || []).map(game => ({
        gameId: game.gameId,
        courtName: room.locationName,
        gameType: game.players.length === 2 ? 'Singles' : 'Doubles',
        players: game.players.map(player => ({
          id: player.userId,
          name: player.nickname,
          rankLevel: player.rank,
          team: player.team || null,
          type: 'unknown'
        })),
        maxPlayers: game.players.length,
        status: game.status === "WAITING"
          ? ""
          : game.status === "ONGOING"
          ? ""
          : game.status === "FINISHED"
          ? ""
          : "",
        createdBy: "",
        createdAt: new Date(`${game.date}T${game.time}`),
        isMine: Number(room.managerId) === Number(currentUserId)
      }));

      setGames(parsedGames);
    } catch (error) {
      console.error('   ', error);
      setGames([]);
    }
  }, [roomId, currentUserId]);

  //   
  useEffect(() => {
    const timer = setInterval(() => setCurrentTime(new Date()), 1000);
    return () => clearInterval(timer);
  }, []);

  //   
  useEffect(() => {
    if (!roomId) return;

    fetchManualWaitlist();
    fetchAutoWaitlist();
    fetchGamelist();
  }, [roomId, currentUserId, fetchManualWaitlist, fetchAutoWaitlist, fetchGamelist]);

  //    
  const handleManualRegister = async () => {
    try {
      await axios.post(
        `/api/match/manual/queue/gym?userId=${currentUserId}&roomId=${roomId}`,
        {},
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert('   .');
      await fetchManualWaitlist();
    } catch (error) {
      console.error('      ', error);
      alert('       .');
    }
  };
  
  const handleAutoRegister = () => setModalTypeOpen(true);

  //     
  const handleCancelManualRegister = async () => {
    try {
      await axios.delete(
        `/api/match/manual/queue?userId=${currentUserId}`,
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert('   .');
      await fetchManualWaitlist();
    } catch (error) {
      console.error('    ', error);
      alert('      .');
    }
  };

  //   
  const handleManualMatchCreate = async () => {
    if (selected.length !== 2 && selected.length !== 4) {
      alert("2  4   .");
      return;
    }

    try {
      await axios.post(
        `/api/match/manual/games/${roomId}?requesterId=${currentUserId}`,
        { userIds: selected },
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );

      alert("   .");
      setSelected([]);
      await fetchManualWaitlist(); 
      await fetchGamelist();    
    } catch (error) {
      console.error("  ", error);
      alert("    .");
    }
  };

  //    
  const handleManualMatchCancel = () => setSelected([]);

  //    
  const handleAddWaitlistByType = async (gameType) => {
    const requiredMatchCount = gameType === 'Singles' ? 2 : 4;

    try {
      await axios.post(
        `/api/match/auto/queue/gym?userId=${currentUserId}&roomId=${roomId}`,
        { requiredMatchCount },
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert(`${gameType === "Singles" ? "" : ""}    .`);
      setModalTypeOpen(false);
      fetchAutoWaitlist();
    } catch (error) {
      console.error('   ', error);
      alert('     .');
    }
  };

  //     
  const handleCancelAutoRegister = async () => {
    try {
      await axios.delete(
        `/api/match/auto/queue?userId=${currentUserId}`, 
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true 
        }
    );
      alert('   .');
      fetchAutoWaitlist();
    } catch (error) {
      console.error('    ', error);
      alert('      .');
    }
  };

  //   
  const handleStartAutoMatch = async () => {
    try {
      const response = await axios.post(
        `/api/match/auto/games/${roomId}`,
        {},
        { 
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );

      const { userIds, gameId, date, time } = response.data;

      const matchedPlayers = autoWaitlist.filter(p =>
        userIds.includes(p.id) || userIds.includes(Number(p.id))
      );

      const gameType = userIds.length === 2 ? 'Singles' : 'Doubles';

      const newGame = {
        id: gameId,
        courtName,
        gameType,
        players: matchedPlayers,
        maxPlayers: userIds.length,
        status: 'Ready',
        createdBy: '',
        createdAt: new Date(`${date}T${time}`),
        isMine: false
      };

      setGames(prev => [...prev, newGame]);
      setAutoWaitlist(prev => prev.filter(p => !userIds.includes(p.id)));
      alert('  .');
    } catch (error) {
      console.error('   ', error);
      alert('    .');
    }
  };

  //  
  const handleStartGame = async (gameId) => {
    try {
      const res = await axios.patch(
        `/api/game/${gameId}/start`,
        {},
        {
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert(res.data.message || " .");
      await fetchGamelist(); //  
    } catch (e) {
      if (e.response && e.response.data && e.response.data.error) {
        alert(e.response.data.error);
      } else {
        alert("    .");
      }
    }
  };

  //  
  const handleGameComplete = async (gameId) => {
    try {
      const res = await axios.patch(
        `/api/game/${gameId}/complete`,
        {},
        {
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert(res.data.message || " .");
      await fetchGamelist();
      await fetchManualWaitlist();
      await fetchAutoWaitlist();
    } catch (e) {
      if (e.response && e.response.data && e.response.data.error) {
        alert(e.response.data.error);
      } else {
        alert("     .");
      }
    }
  };

  //  
  async function submitGameResult({ gameId, userId, opponentId, scoreTeamA, scoreTeamB }) {
    try {
      const res = await axios.post(
        '/api/game/result',
        {
          gameId,
          userId,
          opponentId,
          scoreTeamA,
          scoreTeamB,
          completed: true
        },
        {
          headers: { 'Content-Type': 'application/json' },
          withCredentials: true
        }
      );
      alert(res.data || "   .");
    } catch (e) {
      if (e.response && e.response.data) {
        alert(e.response.data);
      } else {
        alert("     .");
      }
      throw e;
    }
  }

  return (
    <div className="pm-current-matching-wrapper">
      <Header />
      <div className="pm-body-center">
        <div className="pm-current-matching-content">
          <div className="pm-court-matching-wrap">
            {/*    */}
            <div className="pm-info-card">
              <div className="pm-info-row">
                <div>
                  <div className="pm-main-title">{headerTitle}</div>
                </div>
                <div className="pm-clock-box">
                  <Clock style={{ width: 18, height: 18, marginRight: 4 }} />
                  {currentTime.toLocaleTimeString()}
                </div>
              </div>
              <div className="pm-sub-row">
                <MapPin style={{ width: 16, height: 16, marginRight: 4 }} />
                <span className="pm-court-name">{courtName}</span>
                <Badge color="pm-black"> </Badge>
              </div>
              <div className="pm-sub-address">{courtAddr}</div>
              <div className="pm-checkbox-row">
                <input
                  type="checkbox"
                  checked
                  disabled
                  id="auto-match"
                />
                <label htmlFor="auto-match" className="pm-auto-label">
                     
                </label>
              </div>
            </div>

            {/*   */}
            <div className="pm-main-panel-grid">
              {/* :   */}
              <div className="pm-game-rooms-card">
                <div className="pm-panel-header">
                  <Users style={{ width: 18, height: 18, marginRight: 5 }} />
                  <span>  </span>
                  <Badge color="gray">{games.length}</Badge>
                </div>
                <div className="pm-panel-desc">       </div>
                {games.map(room => (
                  <div key={room.gameId} className="pm-game-room-box">
                    <div className="pm-room-header-row">
                      <div>
                        <Badge color="gray">{gameTypeLabel[room.gameType]}</Badge>
                        {room.status === "" && (
                          <Badge color="yellow"> </Badge>
                        )}
                        {room.status === "" && (
                          <Badge color="green"> </Badge>
                        )}
                        {room.status === "" && (
                          <Badge color="red"></Badge>
                        )}
                        {room.status === "" && (
                          <Badge color="black"></Badge>
                        )}
                      </div>
                      <span className="pm-room-player-count">
                        {room.players.length}/{room.maxPlayers} 
                      </span>
                    </div>
                    <div className="pm-players-list">
                      {room.players.map(player => (
                        <div key={player.id} className="pm-player-row">
                          <div className="pm-avatar">{player.name.split(" ").map(n => n[0]).join("")}</div>
                          <span className="pm-player-name">{player.name}</span>
                          <Badge color={rankColor[player.rankLevel]}>
                            {player.rankLevel}
                          </Badge>
                        </div>
                      ))}
                      {/*   */}
                      {room.status === "" && (
                        <Button className="pm-join-btn pm-set-team-btn"
                          onClick={() => {
                            setModalRoom(room);
                            setTeamModalRoomId(room.gameId);
                            setTeamModalOpen(true);
                          }}>
                           
                        </Button>
                      )}
                      {room.status === "" && (
                        <Button className="pm-join-btn pm-game-start-btn"
                          onClick={() => {
                            handleStartGame(room.gameId);
                          }}>
                           
                        </Button>
                      )}
                      {room.status === "" && (
                        <Button className="pm-join-btn pm-game-finish-btn"
                          onClick={() => {
                            handleGameComplete(room.gameId);
                          }}>
                           
                        </Button>
                      )}
                      {room.status === "" && (
                        <Button className="pm-join-btn pm-game-result-btn"
                          onClick={() => {
                            setModalRoom(room);
                            setModalOpen(true);
                          }}>
                           
                        </Button>
                      )}
                    </div>
                    <div className="pm-room-created-at">
                      {room.createdBy}    {room.createdAt.toLocaleTimeString()}
                    </div>
                  </div>
                ))}
              </div>

              {/* :   */}
              <div className="pm-waitlist-card">
                {/*   () */}
                <div>
                  <div className="pm-panel-header" style={{ alignItems: "center", gap: 12 }}>
                    <Users style={{ width: 18, height: 18, marginRight: 5 }} />
                    <span>  </span>
                    <Badge color="gray">{manualWaitlist.length}</Badge>
                    <div style={{ display: "flex", gap: "8px", marginLeft: "auto" }}>
                      {!isAdmin && (
                        <>
                          <Button className="pm-create-btn" onClick={handleManualRegister}>  </Button>
                          <Button className="pm-create-btn" onClick={handleCancelManualRegister}>  </Button>
                        </>
                      )}
                    </div>
                  </div>
                  {isAdmin && (
                    <div className="pm-panel-desc">   (2=, 4=)     </div>
                  )}
                  <div className="pm-waitlist-list">
                    {manualWaitlist.map(player => (
                      <div className="pm-waitlist-row" key={player.id}>
                        {isAdmin && (
                          <input
                            type="checkbox"
                            checked={selected.includes(player.id)}
                            onChange={() =>
                              setSelected(selected.includes(player.id)
                                ? selected.filter(id => id !== player.id)
                                : [...selected, player.id])
                            }
                            style={{ marginRight: 8 }}
                          />
                        )}
                        <div className="pm-wait-avatar">{player.name.split(" ").map(n => n[0]).join("")}</div>
                        <span className="pm-wait-name">{player.name}</span>
                        <Badge color={rankColor[player.rankLevel]}>
                          {player.rankLevel}
                        </Badge>
                      </div>
                    ))}
                  </div>
                  <div style={{ marginTop: 14 }}>
                    {isAdmin && (
                      <>
                        <Button
                          onClick={handleManualMatchCreate}
                          disabled={selected.length !== 2 && selected.length !== 4}
                          style={{
                            marginRight: 8,
                            background: '#e8e3fd',
                            color: '#6930c3',
                            borderRadius: 6
                          }}
                        >
                           
                        </Button>
                        <Button
                          onClick={handleManualMatchCancel}
                          style={{
                            background: '#ececec',
                            color: '#555',
                            borderRadius: 6
                          }}
                        >
                           
                        </Button>
                      </>
                    )}
                  </div>
                </div>
                <hr style={{ margin: "28px 0" }} />
                {/*   () */}
                <div>
                  <div className="pm-panel-header" style={{ alignItems: "center", gap: 12 }}>
                    <Users style={{ width: 18, height: 18, marginRight: 5 }} />
                    <span>  </span>
                    <Badge color="gray">{autoWaitlist.length}</Badge>
                    <div style={{ display: "flex", gap: "8px", marginLeft: "auto" }}>
                      {!isAdmin && (
                        <>
                          <Button className="pm-create-btn" onClick={handleAutoRegister}>  </Button>
                          <Button className="pm-create-btn" onClick={handleCancelAutoRegister}>  </Button>
                        </>
                      )}
                    </div>
                  </div>
                  {isAdmin && (
                    <div className="pm-panel-desc">    </div>
                  )}
                  <div className="pm-waitlist-list">
                    {autoWaitlist.map(player => (
                      <div className="pm-waitlist-row" key={player.id}>
                        <div className="pm-wait-avatar">{player.name.split(" ").map(n => n[0]).join("")}</div>
                        <span className="pm-wait-name">{player.name}</span>
                        <Badge color={rankColor[player.rankLevel]}>
                          {player.rankLevel}
                        </Badge>
                      </div>
                    ))}
                  </div>
                  <div style={{
                    marginTop: 14, display: "flex", justifyContent: "center"
                  }}>
                    {isAdmin && (
                      <button
                        className="pm-create-btn"
                        onClick={handleStartAutoMatch}
                      >
                          
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* ========  ======== */}
        <SelectTypeModal
          open={modalTypeOpen}
          onClose={() => setModalTypeOpen(false)}
          onSelect={handleAddWaitlistByType}
        />

        <GameResultModal
          visible={modalOpen}
          room={modalRoom}
          onClose={() => setModalOpen(false)}
          onResultSaved={() => {
            fetchGamelist();
            fetchManualWaitlist();
            fetchAutoWaitlist();
          }}
          submitGameResult={submitGameResult}
        />

        <TeamSettingModal
          visible={teamModalOpen}
          onClose={() => setTeamModalOpen(false)}
          players={modalRoom?.players || []}
          gameId={teamModalRoomId}
          onSetTeams={(teamA, teamB) => {
            fetchGamelist();
          }}
        />
        
        {isAdmin ? (
          <div style={{ display: "flex", justifyContent: "center", margin: "3px 0" }}>
            <Button
              className="pm-delete-button"
              onClick={async () => {
                try {
                  await axios.delete(
                    `/api/game-room/${roomId}`,
                    { withCredentials: true }
                  );
                  alert(" .");
                  navigate("/pre-matching");
                } catch (error) {
                  console.error("  ", error);
                  alert("    .");
                }
              }}
            >
               
            </Button>
          </div>
        ) : (
          <div style={{ display: "flex", justifyContent: "center", margin: "3px 0" }}>
            <Button
              className="pm-exit-button"
              onClick={async () => {
                try {
                  await axios.delete(`
                    /api/users/${currentUserId}/game-room`,
                    { withCredentials: true }
                  );
                  alert(" .");
                  navigate("/pre-matching");
                } catch (error) {
                  console.error("  ", error);
                  alert("    .");
                }
              }}
            >
               
            </Button>
          </div>
        )}
      </div>
      <Footer />
    </div>
  );
}
