import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, Navigate } from 'react-router-dom';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../styles/MyPage.css';
import axios from 'axios';

function MyPage() {
  const { isAuthenticated } = useSelector(state => state.auth);
  const [userData, setUserData] = useState(null);

  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const response = await axios.get('/api/users/myinfo', {
          withCredentials: true,
      });
      setUserData(response.data);
      } catch (error) {
        console.error('  :', error);
      }
    };

    fetchUserInfo();
  }, []);

  if (!isAuthenticated) {
    return <Navigate to="/login" replace/>;
  }

  return (
    <div className="mypage-page-wrapper">
      <Header/>

      <div className="mypage-page-content">
        <div className="mypage-container">
          <h2 className="mypage-title"> </h2>

          <div className="mypage-info">
            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">{userData?.name || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">{userData?.email || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">{userData?.phone || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">{userData?.nickname || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">{userData?.gender || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">{userData?.rank || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              {/* <span className="info-value">{userData?.profile?.ageGroup || ''}</span> */}
              <span className="info-value">{userData?.ageGroup || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"> </span>
              <span className="info-value">{userData?.playStyle || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"> </span>
              <span className="info-value">{userData?.gameType || ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label">MMR</span>
              <span className="info-value">{userData?.rating ?? ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"> </span>
              <span className="info-value">{userData?.gamesPlayed ?? ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"> </span>
              <span className="info-value">{userData?.winsCount ?? ''}</span>
            </div>

            <div className="info-row">
              <span className="info-label"></span>
              <span className="info-value">
                {userData?.winRate !== undefined ? `${Math.round(userData.winRate * 100)}%` : ''}
              </span>
            </div>
          </div>

          <div className="mypage-actions">
            <button className="action-btn">  </button>
            <button className="action-btn">  </button>
            <button className="action-btn"> </button>
            <button className="action-btn">  </button>
            <Link to="/mypage/match-history" className="action-btn match-history-link">
                
            </Link>
            <button className="action-btn">  </button>
          </div>
        </div>
      </div>

      <Footer/>
    </div>
  );
}

export default MyPage;
