import React, { useState, useRef, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { logout } from '../redux/authSlice';
import '../styles/Header.css';
import logoImage from '../assets/shuttleplay_main_logo.png';
import axios from 'axios';

function Header() {
  const { isAuthenticated } = useSelector(state => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const dropdownRef = useRef(null);

  const handleLogout = async () => {
    try {
      await axios.post('/logout', {}, { withCredentials: true });
      localStorage.removeItem('user');
      dispatch(logout());
      setDropdownOpen(false);
      navigate('/main');
    } catch (error) {
      console.error(' :', error);
    }
  };

  const toggleDropdown = () => {
    setDropdownOpen(prev => !prev);
  };

  useEffect(() => {
    function handleClickOutside(event) {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setDropdownOpen(false);
      }
    }
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <header className="header">
      <div className="header-left">
        <Link to="/main">
          <img src={logoImage} alt="ShuttlePlay Logo" className="logo-image"/>
        </Link>
      </div>

      <nav className="header-center">
        <ul className="nav-menu">
          <li><Link to="/pre-matching"> </Link></li>
          <li><Link to="/current-matching"> </Link></li>
          <li><Link to="/competition"> </Link></li>
          <li><Link to="/reservation"> </Link></li>
          <li><Link to="/club"> </Link></li>
          <li><Link to="/community"></Link></li>
        </ul>
      </nav>

      <div className="header-right">
        <i className="bi bi-chat-dots" title=""></i>
        <i className="bi bi-bell" title=""></i>

        {!isAuthenticated ? (
          <Link to="/login" className="header-login-link">
            <button className="header-login-btn"></button>
          </Link>
        ) : (
          <div className="profile-container" ref={dropdownRef}>
            <button className="profile-btn" onClick={toggleDropdown}>
              <i className="bi bi-person-circle"></i>
            </button>
            {dropdownOpen && (
              <div className="dropdown-menu">
                <Link to="/mypage" className="dropdown-item" onClick={() => setDropdownOpen(false)}></Link>
                <button className="dropdown-item logout-item" onClick={handleLogout}></button>
              </div>
            )}
          </div>
        )}
      </div>
    </header>
  );
}

export default Header;
