import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Header from '../components/Header';
import Footer from '../components/Footer';
import '../styles/PreMatching.css';
import logo from '../assets/shuttleplay_main_logo.png';
import AutoGameRoomMatching from './AutoGameRoomMatching';
import { useSelector } from 'react-redux';
import axios from 'axios';

//   
function PreMatchingCreateRoomModal({ open, onClose, onCreate }) {
  const [mode, setMode] = useState('');

  const [formGym, setFormGym] = useState({
    venue: '', address: '', latitude: '', longitude: '',
    courtName: '', courtAddress: '', date: '', time: '',
  });

  const [formLocal, setFormLocal] = useState({
    location: '', latitude: '', longitude: '', date: '', time: '',
  });

  useEffect(() => {
    if (open) {
      setMode('');
      setFormGym ({ venue:'', address:'', latitude:'', longitude:'', courtName:'', courtAddress:'', date:'', time:'' });
      setFormLocal({ location:'', latitude:'', longitude:'', date:'', time:'' });
    }
  }, [open]);

  const handleChangeGym = e => setFormGym (p=>({...p,[e.target.name]:e.target.value}));
  const handleChangeLocal = e => setFormLocal(p=>({...p,[e.target.name]:e.target.value}));

  const handleSubmit = e => {
    e.preventDefault();
    mode === ''
      ? onCreate({ ...formGym,  mode:'' })
      : onCreate({ ...formLocal,mode:'' });
    onClose();
  };

  if (!open) return null;

  return (
    <div className="pre-modal-backdrop">
      <div className="pre-modal">
        <div style={{display:'flex',gap:12,marginBottom:20,justifyContent:'center'}}>
          <button className={`pre-modal-mode-btn${mode===''?' selected':''}`} onClick={()=>setMode('')}>
              
          </button>
          <button className={`pre-modal-mode-btn${mode===''?' selected':''}`} onClick={()=>setMode('')}>
              
          </button>
        </div>

        <form className="pre-modal-form" onSubmit={handleSubmit}>
          {mode==='' ? (
            <>
              <input name="venue" placeholder="" value={formGym.venue} onChange={handleChangeGym} required/>
              <input name="address" placeholder="" value={formGym.address} onChange={handleChangeGym} required/>
              <input name="latitude" placeholder="" value={formGym.latitude} onChange={handleChangeGym} required/>
              <input name="longitude" placeholder="" value={formGym.longitude} onChange={handleChangeGym} required/>
              <input name="courtName" placeholder=" " value={formGym.courtName} onChange={handleChangeGym} required/>
              <input name="courtAddress" placeholder=" " value={formGym.courtAddress} onChange={handleChangeGym} required/>
              <input name="date" type="date" placeholder="" value={formGym.date} onChange={handleChangeGym} required/>
              <input name="time" type="time" placeholder="" value={formGym.time} onChange={handleChangeGym} required/>
            </>
          ) : (
            <>
              <input name="location" placeholder="()" value={formLocal.location} onChange={handleChangeLocal} required/>
              <input name="latitude" placeholder="" value={formLocal.latitude} onChange={handleChangeLocal} required/>
              <input name="longitude" placeholder="" value={formLocal.longitude} onChange={handleChangeLocal} required/>
              <input name="date" type="date" placeholder="" value={formLocal.date} onChange={handleChangeLocal} required/>
              <input name="time" type="time" placeholder="" value={formLocal.time} onChange={handleChangeLocal} required/>
            </>
          )}
          <button className="pre-modal-create-btn" type="submit"> </button>
          <button className="pre-modal-cancel-btn" type="button" onClick={onClose}></button>
        </form>
      </div>
    </div>
  );
}

//  
function PreMatching() {
  const navigate = useNavigate();
  const userId = useSelector(s=>s.auth.user?.userId);
  const [step, setStep] = useState('selectMode');
  const [selectedMode, setSelectedMode] = useState('');
  const [selectedVenue, setSelectedVenue] = useState('');
  const [selectedDate, setSelectedDate] = useState('');
  const [selectedTime, setSelectedTime] = useState('');
  const [createModalOpen, setCreateModalOpen] = useState(false);
  const [roomList, setRoomList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  // /   location  
  const gymOptions = [
    {
      label: '  ',
      value: '  ',
      courtName: ' ',
      courtAddress: '   280',
      latitude: '35.8835',
      longitude: '128.8172',
      address: '   280'
    },
    {
      label: '  ',
      value: '  ',
      courtName: '  ',
      courtAddress: '   246',
      latitude: '35.8251',
      longitude: '128.7376',
      address: '   246'
    }
  ];
  const localOptions = [
    {
      label: ' ',
      value: ' ',
      location: ' ',
      courtName: ' ',
      courtAddress: ' ',
      latitude: '35.8381',
      longitude: '128.7552'
    },
    {
      label: ' ',
      value: ' ',
      location: ' ',
      courtName: ' ',
      courtAddress: ' ',
      latitude: '35.8999',
      longitude: '128.7962'
    }
  ];

  //    location   
  const [selectedLocation, setSelectedLocation] = useState({});

  useEffect(() => {
    if (selectedMode === '') {
      const found = gymOptions.find(opt => opt.value === selectedVenue);
      setSelectedLocation(found || {});
    } else {
      const found = localOptions.find(opt => opt.value === selectedVenue);
      setSelectedLocation(found || {});
    }
  }, [selectedVenue, selectedMode]);

  const handleFindRooms = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setRoomList([]);
    try {
      let url = '';
      let body = {};
      if (selectedMode === '') {
        url = `/api/match/manual/rooms/gym?userId=${userId}`;
        body = {
          location: {
            courtName: selectedLocation.courtName || '',
            courtAddress: selectedLocation.courtAddress || '',
            latitude: selectedLocation.latitude || '',
            longitude: selectedLocation.longitude || '',
            address: selectedLocation.address || ''
          },
          date: selectedDate,
          time: selectedTime,
        };
      } else {
        url = `/api/match/manual/rooms/location?userId=${userId}`;
        body = {
          location: {
            courtName: selectedLocation.courtName || '',
            courtAddress: selectedLocation.courtAddress || '',
            latitude: selectedLocation.latitude || '',
            longitude: selectedLocation.longitude || '',
            //  location()  
            location: selectedLocation.location || ''
          },
          date: selectedDate,
          time: selectedTime,
        };
      }
      const res = await axios.post(url, body, {
        headers: { userId },
        withCredentials: true
      });
      setRoomList(res.data.rooms || []);
    } catch (err) {
      setError('  .');
    }
    setLoading(false);
  };

  //  
  const timeOptions = [];
  for(let h=0;h<24;h++){
    timeOptions.push(`${String(h).padStart(2,'0')}:00`);
    timeOptions.push(`${String(h).padStart(2,'0')}:30`);
  }

  const getBtnClass = active => 'pre-select-mode-btn' + (active ? ' pre-select-active' : '');

  // 
  return (
    <div className="pre-matching-wrapper">
      <Header/>

      <div className="pre-body-center">
        <PreMatchingCreateRoomModal
          open={createModalOpen}
          onClose={() => setCreateModalOpen(false)}
          onCreate={() => {}}
        />

        {/*   /    */}
        {step === 'selectMode' && (
          <div className="pre-select-mode-card">
            <img src={logo} alt="ShuttlePlay " className="pre-card-logo"/>
            <div className="pre-card-content">
              <h2>  !</h2>
              <div className="pre-select-mode-buttons">
                <button
                  className={getBtnClass(step === 'autoInput')}
                  onClick={() => setStep('autoInput')}
                > </button>
                <button
                  className={getBtnClass(step === 'filter')}
                  onClick={() => setStep('filter')}
                > </button>
              </div>
            </div>
          </div>
        )}

        {/*       */}
        {step === 'autoInput' && (
          <AutoGameRoomMatching onClose={() => setStep('selectMode')}/>
        )}

        {/*    */}
        {step === 'filter' && (
          <>
            <div className="pre-filter-bar">
              <div
                className="pre-filter-items"
                style={{display:'flex',gap:20,flexWrap:'wrap',alignItems:'center'}}
              >
                <div className="pre-filter-item">
                  <span></span>
                  <select value={selectedMode} onChange={e=>{
                    setSelectedMode(e.target.value);
                    setSelectedVenue('');
                  }}>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                </div>
                <div className="pre-filter-item">
                  <span></span>
                  <select
                    value={selectedVenue}
                    onChange={e=>setSelectedVenue(e.target.value)}
                  >
                    <option value=""> </option>
                    {selectedMode === ''
                      ? gymOptions.map(opt => (
                          <option key={opt.value} value={opt.value}>{opt.label}</option>
                        ))
                      : localOptions.map(opt => (
                          <option key={opt.value} value={opt.value}>{opt.label}</option>
                        ))
                    }
                  </select>
                </div>
                <div className="pre-filter-item">
                  <span></span>
                  <input
                    type="date"
                    value={selectedDate}
                    onChange={e=>setSelectedDate(e.target.value)}
                  />
                </div>
                <div className="pre-filter-item">
                  <span></span>
                  <select value={selectedTime} onChange={e=>setSelectedTime(e.target.value)}>
                    {timeOptions.map(t => <option key={t}>{t}</option>)}
                  </select>
                </div>
                <div style={{display:'flex',gap:10}}>
                  <button
                    className="pre-filter-apply-btn"
                    onClick={handleFindRooms}
                  >  </button>
                  <button
                    className="pre-create-btn"
                    onClick={() => setCreateModalOpen(true)}
                  > </button>
                </div>
              </div>
            </div>
            {/*     */}
            <div style={{width:'100%'}}>
              {loading && (
                <div className="auto-message">  ...</div>
              )}
              {error && (
                <div className="auto-message" style={{color:'red'}}>{error}</div>
              )}
              {!loading && !error && roomList.length === 0 && (
                <div className="auto-message">   .</div>
              )}
              {!loading && !error && roomList.length > 0 && (
                <div className="pre-card-grid">
                  {roomList.map((room, idx) => (
                    <div className="pre-card" key={room.roomId || idx}>
                      <div className="pre-card-thumbnail">
                        <span className="pre-thumbnail-text">
                          {room.mode === '' ? '  : ' : '  : '} {room.title ? room.title : (room.mode === '' ? '' : '')}
                        </span>
                      </div>
                      <div className="pre-card-body">
                        <h4>{room.locationName || ' '}</h4>
                        <div className="pre-alt-info">
                          <div><span className="label">:</span> {selectedDate}</div>
                          <div><span className="label">:</span> {selectedTime}</div>
                        </div>
                        <div className="pre-card-bottom">
                          <button
                            className="pre-enter-btn"
                            onClick={() => navigate(`/manual-room/${room.roomId}`)}
                          ></button>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </>
        )}
      </div>

      <div style={{marginTop:'auto'}}><Footer/></div>
    </div>
  );
}

export default PreMatching;
